import * as React from "react"
import { navigate } from "gatsby"
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  SwipeableDrawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@mui/material"
import MenuIcon from "@mui/icons-material/Menu"
import DirectionsBoatIcon from "@mui/icons-material/DirectionsBoat"
import FlightIcon from "@mui/icons-material/Flight"
import VpnLockIcon from "@mui/icons-material/VpnLock"
import HomeIcon from "@mui/icons-material/Home"
import { GitHub } from "@mui/icons-material"

export default function Header({ siteTitle }: any): React.ReactElement {
  const [state, setState] = React.useState({
    drawer: false,
  })

  const toggleDrawer = (open: boolean) => () => {
    setState({ ...state, drawer: open })
  }

  return (
    <header>
      <AppBar position="static">
        <Toolbar>
          <IconButton color="inherit" onClick={toggleDrawer(true)} size="large">
            <MenuIcon />
          </IconButton>
          <Typography style={{ flexGrow: 1 }}>{siteTitle}</Typography>
          <IconButton
            color="inherit"
            href="https://github.com/JasonKhew96/bangumi"
            size="large"
          >
            <GitHub />
          </IconButton>
        </Toolbar>
      </AppBar>
      <SwipeableDrawer
        open={state.drawer}
        onOpen={toggleDrawer(true)}
        onClose={toggleDrawer(false)}
      >
        <List>
          <ListItem button key="" onClick={() => navigate("/")}>
            <ListItemIcon>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
          <ListItem
            button
            key=""
            onClick={() => navigate("/bilibili/")}
          >
            <ListItemIcon>
              <DirectionsBoatIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
          <ListItem
            button
            key=""
            onClick={() => navigate("/bilibili_sea/")}
          >
            <ListItemIcon>
              <FlightIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
          <ListItem
            button
            key=""
            onClick={() => navigate("/anigamer/")}
          >
            <ListItemIcon>
              <VpnLockIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </List>
      </SwipeableDrawer>
    </header>
  )
}

